*{
  margin: 0;
  padding:0;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image:url("sky4.jpg");
  background-repeat: repeat-x; 
  display:flex;
  justify-content:center;
  align-items:center;
}

.putcenter{
  text-emphasis: center;
  position: absolute;
  top:20px;
  left:50%;
 transform:translateX(-50%);
}
.slide-img{
  max-width: 100%;
  height:550px;
  border-radius: 5px;
}
.slide-img:hover{
  box-shadow:0px 4px 18px 8px rgba(183, 143, 201, 0.8);
}
.back{
  position: absolute;
  top:50px;
  left:10%;
  font-size: 2em;
  padding: 5px;
  background-color:rgba(51, 169, 238, 0.3);
  color:rgb(8, 138, 105);
  transform:translateX(-50%);
}
.next{
  position: absolute;
  top:50px;
  right:10%;
  font-size: 2em;
  padding: 5px;
  background-color:rgba(51, 169, 238, 0.3);
  color:rgb(8, 138, 105);
  transform:translateX(50%);
}
.auto{
  color:rgb(198, 227, 243);
  font-size: x-large;
  padding: 15px;
  border-radius: 15px;
  background-color: #07267c;
  position: absolute;
  bottom:-10%;
  left:60%;
  transform: translate(-50%,-50%); 
}
.stop{
    color:rgb(198, 227, 243);
    font-size: x-large;
    padding: 15px;
    border-radius: 15px;
    background-color: #7c072e;
    position: absolute;
    bottom:-10%;
    left:40%;
    transform: translate(-50%,-50%); 
}
/*  */ 
button{
  position:relative;
  margin:0px;
  padding:20px 60px;
  background:transparent;
  border:5px solid white;
  font-family:sans-serif;
  color:white;
  cursor:pointer;
}
button::after
{
  position:absolute;
  content:" ";
  width:40px;
  height:5px;
  background-color:red;
  top:-5px;
  left:0px;
  transition: .4s linear;
}
button:hover
{
  box-shadow:0px 4px 18px 8px rgb(116, 11, 161);
}
button:hover::after
{
  left:152px;
}
button::before
{
  position:absolute;
  content:" ";
  width:40px;
  height:5px;
  background-color:red;
  bottom:-5px;
  right:0px;
  transition: .4s linear;
}
button:hover::before
{
  right:152px;
}
